.profile {
  padding-top: 20px;
  margin-right: auto;
  margin-left: auto;
  align-items: center;
  width: 300px;
  display: flex;
  flex-direction: column;
  background-color: #fff;
}

.description {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.avatar {
  display: block;
  width: 100px;
  border-radius: 50%;
  background-color: #f5f4fa;
}

.name {
  padding-top: 40px;
  padding-bottom: 10px;
  font-size: 20px;
  font-weight: 500;
}

.tag {
  padding-bottom: 10px;
  color: #757575;
}

.location {
  padding-bottom: 40px;
  color: #757575;
}

.stats {
  list-style: none;
  display: flex;
  align-items: center;
}

.stats__item {
  display: flex;
  flex-direction: column;
  padding: 26px;
  align-items: center;
  background-color: #e8e2e2;
}

.label {
  color: #757575;
  padding-bottom: 5px;
}

.quantity {
  font-weight: bold;
}
