.item {
  background-color: #fff;
  padding: 10px;
  display: flex;
  align-items: center;
  width: 280px;
  border-radius: 10px;
  margin-bottom: 10px;
}
.item:last-child {
  margin-bottom: 0px;
}

.status__online {
  width: 15px;
  height: 15px;
  margin-left: 10px;
  margin-right: 10px;
  border-radius: 50%;
  background-color: green;
}

.status__offline {
  width: 15px;
  height: 15px;
  margin-left: 10px;
  margin-right: 10px;
  border-radius: 50%;
  background-color: red;
}

.avatar {
  margin-right: 15px;
}

.name {
  font-size: 22px;
  font-weight: 500;
}
